.clearfix {
    zoom: 1
}

.clearfix:after,
.clearfix:before {
    content: "";
    display: table
}

.clearfix:after {
    clear: both
}

.hide {
    display: none
}

.show {
    display: block
}

.hidden,
.invisible {
    visibility: hidden
}

.hidden {
    display: none
}

.hidden-desktop,
.visible-phone,
.visible-tablet {
    display: none !important
}

@media (max-width:767px) {
